  }
})
export default class ApontamentosRelatorios extends Vue {
  /* eslint-disable indent */
  private modalReports: boolean = false;
  private nameBox: any = null;
  private items: Array < any > = [{
    icon: 'mdi-file-chart',
    title: 'Relatrios',
    items: [{
        title: 'Apontamento da Produo'
      },
      {
        title: 'Apontamento de Sequestradores'
      },
      {
        title: 'Projetado/Produzido'
      },
      {
        title: 'Produo Apontada X Produo Armazenada'
      },
      {
        title: 'Auditoria Entre Connect X SISAP'
      },
      {
        title: 'Apontamento Rob CMS'
      },
      {
        title: 'Apontamento x Impressoras x Sada Tnel'
      },
      {
        title: 'Apontamento de Resfriados'
      },
      {
        title: 'Apontamento de Refeitrio'
      },
      {
        title: 'Matrias Primas Endereadas'
      },
      {
        title: 'Histrico Cmara/Pallets'
      },
      {
        title: 'Controle de Alteraes'
      },
      {
        title: 'Divergncias Endereamento'
      },
      {
        title: 'Produo por Produtor'
      },
      {
        title: 'Grfico'
      },
      {
        title: 'Mquinas'
      },
      {
        title: 'Paradas e OCorrncias de Equipamentos'
      },
      {
        title: 'Grfico de Paradas e Ocorncias'
      },
      {
        title: 'Desempenho Operacional'
      },
      {
        title: 'Grfico de Paradas'
      },
      {
        title: 'Grfico de Ritmos'
      },
      {
        title: 'Gesto de Estoques - Emb. Primria'
      },
    ]
  }, ]

  public selectReport(item: any): void {

    switch (item.title) {
      case 'Apontamento de Resfriados':
        this.nameBox = 'Impresso de Apontamento de Resfriados';
        break;
      default:
        break;
    }
  }

  public closeModal(): void {
    this.modalReports = false;
  }
}
